@use 'base/variables' as v;
@use 'base/mixins' as m;


.barra-mobile {
    background-color: v.$azul;
    padding: 3rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include m.tablet {
        display: none;
    }

    h1 {
        color: v.$blanco;
        margin: 0;
        font-size: 3rem;
    }

    .menu {
        img {
            width: .8rem;
            &:hover {
                cursor: pointer;
            }
        }
    }
}


.barra {
    display: none;
    

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: v.$blanco;
        padding: 1rem 2rem;
        box-shadow: 1px 12px 9px -13px rgba( 0 , 0 , 0, .4);
    }

    p {
        margin: 0;
        font-size: 1.4rem;

        span {
            font-weight: 700;
            color: v.$azul;
        }
    }

    .cerrar-sesion {
        @include m.boton (v.$azul, v.$blanco);
        font-size: 1.4rem;
        padding: 1rem;
        border-radius: 1.2rem;
    }
}