html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.contenedor-ventana {
  width: 50%;
  background-color: #6b7280;
  max-width: 1200px;
  margin: 0 auto;
}

/** Media Queries **/
h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 10rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Montserrat", sans-serif;
  color: #000;
  font-weight: 900;
  font-size: 2.4rem;
  text-align: center;
}
@media (min-width: 768px) {
  .tagline {
    font-size: 3rem;
  }
}

.descripcion-pagina {
  text-align: center;
}

.auth {
  padding-top: 10rem;
}
.auth .personal-collect {
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .auth .personal-collect {
    padding-top: 5rem;
  }
}
.auth input[type=submit] {
  margin-top: 3rem;
  background-color: #0030a6;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 400;
  text-transform: uppercase;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .auth input[type=submit] {
    width: auto;
  }
}
.auth input[type=submit]:hover {
  background-color: #002173;
  cursor: pointer;
}
.auth .acciones {
  margin-top: 3rem;
}
.auth .acciones a {
  color: #0030a6;
}

.formulario {
  margin-top: 3rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  color: #6b7280;
  background-color: #d8dadf;
}
.formulario .campo input #usuarios_id {
  background-color: #000;
}
.formulario .campo select {
  flex: 1;
  padding: 1rem;
  border: none;
  color: #6b7280;
  background-color: #FFFFFF;
}

#usuarios_id[readonly] {
  background-color: rgb(217, 217, 217);
  color: #000;
}

#usuario_id, #deudor_id[readonly] {
  background-color: rgb(217, 217, 217);
  color: #000;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.dashboard {
  background-color: #f3f3f3;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}

.barra-mobile {
  background-color: #0030a6;
  padding: 3rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile h1 {
  color: #FFFFFF;
  margin: 0;
  font-size: 3rem;
}
.barra-mobile .menu img {
  width: 0.8rem;
}
.barra-mobile .menu img:hover {
  cursor: pointer;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #FFFFFF;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
  }
}
.barra p {
  margin: 0;
