}
.barra p span {
  font-weight: 700;
  color: #0030a6;
}
.barra .cerrar-sesion {
  background-color: #0030a6;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 400;
  text-transform: uppercase;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
