}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    width: auto;
  }
}
.barra .cerrar-sesion:hover {
  background-color: #002173;
  cursor: pointer;
}

.sidebar {
  opacity: 0;
  background-color: #0030a6;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  opacity: 1;
  padding: 2rem;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8196078431);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: #0030a6;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  display: none;
  color: #FFFFFF;
  margin: 0;
  text-align: center;
  font-size: 2.5rem;
  font-weight: 400;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar h2 {
    display: flex;
  }
}
.sidebar .contenedor-sidebar h3 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
  font-size: 2.5rem;
  font-weight: 700;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar h3 {
    display: none;
  }
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}
.sidebar .contenedor-sidebar .cerrar-menu img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-property: 300ms;
  font-weight: 400;
  font-size: 2rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #002173;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
    text-align: center;
  }
}
.sidebar .sidebar-icono {
  font-size: 3rem;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  padding: 0.7rem;
}
.sidebar .cerrar-sesion-mobile {
  margin-top: 10rem;
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion {
  background-color: #0030a6;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 400;
  text-transform: uppercase;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile .cerrar-sesion {
    width: auto;
  }
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion:hover {
  background-color: #002173;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile {
    display: none;
  }
}

