#preload-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.preload-spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* Estilo para la ventana emergente */
/* Estilo para el fondo oscurecido */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* Color de fondo oscurecido con transparencia */
  display: none;
  z-index: 1000;
  /* Asegura que esté por encima de otros elementos */
}

/* Estilo para la ventana emergente */
.ventana-emergente {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #f2f2f2;
  /* Cambia esto al color que desees */
  padding: 1rem 5rem 1rem 5rem;
  z-index: 1001;
  /* Asegura que esté por encima del overlay */
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}
.ventana-emergente h4 {
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 700;
  color: #4B5563;
}
.ventana-emergente .contenedor-datos-usuario {
  padding: 0;
}
.ventana-emergente .boton {
  background-color: #0030a6;
  color: #FFFFFF;
  text-transform: uppercase;
  padding: 1rem;
  border: 0;
  margin-bottom: 2rem;
}

/* Estilo para el contenido de la ventana emergente */
.contenido-ventana {
  text-align: center;
}
.contenido-ventana h4 {
  margin-top: 1.5rem;
  margin-bottom: 0;
  font-size: 2rem;
  background-color: #0030a6;
  color: #FFFFFF;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-weight: 400;
}

.formulario-ventana {
  padding-top: 5rem;
  padding-bottom: 5rem;
  border-bottom: 1px solid rgb(212, 212, 212);
  border-top: 1px solid rgb(212, 212, 212);
}
.formulario-ventana .boton {
  margin-top: 3rem;
  background-color: #0030a6;
  padding: 1.5rem 4rem;
  color: #fff;
  font-size: 1.6rem;
  font-weight: 400;
  text-transform: uppercase;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
.formulario-ventana .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario-ventana .campo label {
  flex: 0 0 20rem;
}
.formulario-ventana .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  color: #6b7280;
  background-color: #d8dadf;
}
.formulario-ventana .campo select {
  flex: 1;
  padding: 1rem;
  border: none;
  color: #6b7280;
  background-color: #d8dadf;
}

/* Fondos de situacion */
.situacion {
  color: #FFFFFF;
  padding: 1rem;
  /* Espaciado interno para el contenido */
  text-align: center;
  display: inline-block;
  width: 14rem;
  /* Ancho fijo para el span */
  height: auto;
  /* Altura fija para el span */
  box-sizing: border-box;
}

.fondo-rojo {
  background-color: red;
}

.fondo-cyan {
